import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {Router, browserHistory, Route, IndexRoute} from 'react-router';
import {PostDetail} from "./component/PostDetail";
import {Home} from "./home";
import {PostComponent} from "./component/PostComponent";
import {PhotoComponent} from "./component/PhotoComponent";


ReactDOM.render(
    <Router history={browserHistory}>
        <Route path='/admin' component={Home} name="Home">
            <IndexRoute component={PostComponent} name="Posts"/>
            <Route path='/admin/photos' component={PhotoComponent} name="Photos"/>
            <Route path='/admin/detail/:id' component={PostDetail} name="Detail"/>
        </Route>
    </Router>,
    document.getElementById('example')
);


