import * as React from 'react'
import store from "../store/postStore";
import {FetchData} from "../action/postAction";
import {Post} from "../model/post";
import {PostRowItem} from "./post-row-item";
// include styles
import {ListStickyHeader} from "./listStickyHeader";
import blockStore from "../store/blockStore";
import {Blocking, UnBlocking} from "../action/blockAction";


interface thisState {
    Posts: Post[],
    images: any,
    items: []
}

export class PostComponent extends React.Component<{}, thisState> {

    handleImageLoad(event) {
        console.log('Image loaded ', event.target)
    }

    componentWillMount() {
        this.setState({
            Posts: [],
            images: [
                {
                    original: 'http://lorempixel.com/1000/600/nature/1/',
                    thumbnail: 'http://lorempixel.com/250/150/nature/1/',
                },
                {
                    original: 'http://lorempixel.com/1000/600/nature/2/',
                    thumbnail: 'http://lorempixel.com/250/150/nature/2/'
                },
                {
                    original: 'http://lorempixel.com/1000/600/nature/3/',
                    thumbnail: 'http://lorempixel.com/250/150/nature/3/'
                }
            ],
            items: [
                {
                    headerName: "List A",
                    items: [{
                        title: "items1"
                    }, {
                        title: "items2"
                    }]
                },
                {
                    headerName: "List B",
                    items: [{
                        title: "items1"
                    }, {
                        title: "items2"
                    }]
                },
                {
                    headerName: "List C",
                    items: [{
                        title: "Item C.1"
                    }, {
                        title: "Item C.2"
                    }]
                }
            ]
        })

        store.subscribe(
            () => {
                let state = store.getState();
                this.setState({
                    Posts: state.data
                })

                blockStore.dispatch(UnBlocking());

            }
        )
    }

    componentDidMount() {
        store.dispatch(FetchData());

        blockStore.dispatch(Blocking());

    }

    render() {
        return (
            <div className="row">
                <div className="col-md-6">
                    <ListStickyHeader data={this.state.items}/>
                </div>
                <div className="col-md-6">
                </div>
                <div className="col-md-6">
                    <div className="table-responsive">
                        <table className="table">
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>User Id</th>
                                <th>Title</th>
                                <th>Body</th>
                            </tr>
                            </thead>
                            <tbody>
                            {
                                this.state.Posts ?
                                    this.state.Posts.map((item, index) => {
                                        return <PostRowItem post={item} key={index}/>
                                    }) : <span>No Records</span>
                            }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>

        )
    }
}


