import * as React from 'react'
import {Link} from "react-router";
import {Notification} from 'react-notification';
import BlockUi from 'react-block-ui';
import blockStore from "./store/blockStore";


interface HState {
    isActive: boolean,
    block: boolean
}

export class Home extends React.Component<any, HState> {

    componentWillMount() {
        this.setState({
            isActive: false,
            block: false
        })

        blockStore.subscribe(() => {
            let state = blockStore.getState();

            this.setState({
                block: state.block
            })
        })
    }

    render() {
        return (
            <div>

                <button
                    onClick={() => this.setState({isActive: !this.state.isActive})}
                >Toggle
                </button>

                <Notification
                    isActive={this.state.isActive}
                    message='Notification Message !!!'
                />

                <nav className="navbar navbar-default">
                    <div className="container-fluid">
                        <div className="navbar-header">
                            <Link to="/admin" className="navbar-brand">WebSiteName</Link>
                        </div>
                        <ul className="nav navbar-nav">
                            <li><Link to="/admin" activeClassName="active">Posts</Link></li>
                            <li><Link to="/admin/photos" activeClassName="active">Photos</Link></li>
                        </ul>
                    </div>
                </nav>
                <BlockUi tag="div" blocking={this.state.block}>
                    {this.props.children}
                </BlockUi>
            </div>
        )
    }
}


